@font-face {
  font-family: 'Michroma';
  src: url('./fonts/Michroma-Regular.eot'); /* IE9 Compat Modes */
  src: url('./fonts/Michroma-Regular.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('./fonts/Michroma-Regular.otf') format('opentype'),
    /* Open Type Font */ url('./fonts/Michroma-Regular.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('./fonts/Michroma-Regular.woff')
      format('woff'),
    /* Modern Browsers */ url('./fonts/Michroma-Regular.woff2') format('woff2'); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Michroma', 'Segoe UI',
    'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: env(safe-area-inset-top, 1rem) env(safe-area-inset-right, 1rem)
    env(safe-area-inset-bottom, 1rem) env(safe-area-inset-left, 1rem);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

button {
  outline: none;
}
